import styled from 'styled-components'

const Input = styled.input`
  background: #ffffff;
  mix-blend-mode: normal;
  border: 1px solid #606060;
  border-radius: 10px;
  padding: 21px 19px;
  margin-bottom: 16px;
  width: 100%;

  &::placeholder {
    color: #333333;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 1;
    text-transform: uppercase;
  }
`

export default Input
